import React, { useState } from "react";
import { BoxTooltipTitle } from "../ToolTipsFolder/ToolTips";
import { useDispatch } from "react-redux";
import "./layout.scss";
import { ChangeScrennMode, getLayout } from "../../store/theme";
import { useSelector } from "react-redux";

function Layout() {
  let { layout } = useSelector((state) => state.themeslice);

  let [typeCheck, setTypeCheck] = useState("labtop");

  let chnageMode = (e) => {
    dispatch(ChangeScrennMode(e));
  };

  let dispatch = useDispatch();
  return (
    <div className="icnons-control">
      <div className="icnons-control2">
        <div className="icnons-control3">
          <div className="actionsss">
            {/*" Single"*/}
            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Single layout. Press <text>SHIFT</text> + <text>1</text>{" "}
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  dispatch(getLayout("Single"));
                }}
              >
                <div
                  className={`wraper-icon ${
                    layout === "Single" ? "active" : ""
                  }`}
                >
                  <i className="fa-solid fa-user"></i>
                </div>
              </div>
            </BoxTooltipTitle>

            {/**Fill */}
            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Fill layout. Press <text>SHIFT</text> + <text>2</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  dispatch(getLayout("Fill"));
                }}
              >
                <div
                  className={`wraper-icon ${layout === "Fill" ? "active" : ""}`}
                >
                  <div className="conta-icon">
                    <i className="fa-solid fa-user"></i>
                  </div>
                  <div className="hr"></div>
                  <div className="conta-icon">
                    <i className="fa-solid fa-user"></i>
                  </div>
                </div>
              </div>
            </BoxTooltipTitle>

            {/*"Group "*/}
            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Group layout. Press <text>SHIFT</text> + <text>3</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  dispatch(getLayout("Grid"));
                }}
              >
                <div
                  className={`wraper-icon gap-2 ${
                    layout === "Grid" ? "active" : ""
                  }`}
                  style={{ padding: "1px" }}
                >
                  <div className="conta-icon2">
                    <i
                      className="fa-solid fa-user"
                      style={{ fontSize: "12px" }}
                    ></i>
                  </div>
                  <div className="conta-icon2">
                    <i
                      className="fa-solid fa-user"
                      style={{ fontSize: "12px" }}
                    ></i>
                  </div>
                </div>
              </div>
            </BoxTooltipTitle>
            {/*" Spotlight"*/}
            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Spotlight layout. Press <text>SHIFT</text> + <text>4</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  dispatch(getLayout("mainSup"));
                }}
              >
                <div
                  className={`wraper-icon  gap-2  ${
                    layout === "Main-sub" ? "active" : ""
                  }`}
                >
                  <div className="larg-icon">
                    <i className="fa-solid fa-user"></i>
                  </div>
                  <div className="small-icon">
                    <i className="fa-solid fa-user"></i>
                    <i className="fa-solid fa-user"></i>
                  </div>
                </div>
              </div>
            </BoxTooltipTitle>
            {/**
                    <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  News layout. Press <text>SHIFT</text> + <text>5</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  setCheck("fife");
                }}
              >
                <div
                  className={`wraper-icon  gap-2 ${
                    check === "fife" ? "active" : ""
                  }`}
                >
                  <i className="fa-solid fa-user"></i>
                  <i
                    className="fa-solid fa-square"
                    style={{ fontSize: "17px" }}
                  />
                </div>
              </div>
            </BoxTooltipTitle>

            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Screen layout. Press <text>SHIFT</text> + <text>6</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  setCheck("six");
                }}
              >
                <div
                  className={`wraper-icon gap-2 ${
                    check === "six" ? "active" : ""
                  }`}
                >
                  <div className="small-icon">
                    <i className="fa-solid fa-user"></i>
                  </div>
                  <i
                    className="fa-solid fa-square"
                    style={{ fontSize: "30px" }}
                  />
                </div>
              </div>
            </BoxTooltipTitle>

            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Picture-in-Picture layout. Press <text>SHIFT</text> +{" "}
                  <text>7</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  setCheck("seven");
                }}
              >
                <div
                  className={`wraper-icon  gap-2 ${
                    check === "seven" ? "active" : ""
                  }`}
                >
                  <i className="fa-solid fa-user inside"></i>
                  <i
                    className="fa-solid fa-square"
                    style={{ fontSize: "37px" }}
                  />
                </div>
              </div>
            </BoxTooltipTitle>

            <BoxTooltipTitle
              placement="top"
              backgroundColor="rgb(27, 31, 41)"
              title={
                <span className="titleWithButtons">
                  Cinema layout. Press <text>SHIFT</text> + <text>8</text>
                </span>
              }
            >
              <div
                className="box-icon"
                onClick={(e) => {
                  setCheck("eaght");
                }}
              >
                <div
                  className={`wraper-icon  gap-2 ${
                    check === "eaght" ? "active" : ""
                  }`}
                >
                  <i
                    className="fa-solid fa-square"
                    style={{ fontSize: "37px" }}
                  />
                </div>
              </div>
            </BoxTooltipTitle>
        
        */}
          </div>

          <div className="icnons-mode" style={{ left: "0px " }}>
            <div className="waprer-edit-1">
              <div className="waprer-edit-2">
                <BoxTooltipTitle
                  placement="top"
                  backgroundColor="rgb(27, 31, 41)"
                  title={<span className="titleWithButtons">Landscape</span>}
                >
                  <div
                    className={`contai-edit ${
                      typeCheck === "labtop" && "active"
                    }`}
                    onClick={() => {
                      chnageMode("landscape");
                      setTypeCheck("labtop");
                    }}
                  >
                    <i className="fa-solid fa-laptop" />
                  </div>
                </BoxTooltipTitle>

                <BoxTooltipTitle
                  placement="top"
                  backgroundColor="rgb(27, 31, 41)"
                  title={<span className="titleWithButtons">Portrait</span>}
                >
                  <div
                    className={`contai-edit ${
                      typeCheck === "mobile" && "active"
                    }`}
                    onClick={() => {
                      chnageMode("portrait");
                      setTypeCheck("mobile");
                    }}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 384 512"
                    >
                      {/*!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.*/}
                      <path d="M16 64C16 28.7 44.7 0 80 0H304c35.3 0 64 28.7 64 64V448c0 35.3-28.7 64-64 64H80c-35.3 0-64-28.7-64-64V64zM224 448a32 32 0 1 0 -64 0 32 32 0 1 0 64 0zM304 64H80V384H304V64z" />
                    </svg>
                  </div>
                </BoxTooltipTitle>
              </div>
            </div>
          </div>

          {/**
                     <div className="icnons-edit">
            <div className="waprer-edit-1">
              <div className="waprer-edit-2">
                <BoxTooltipTitle
                  placement="top"
                  backgroundColor="rgb(27, 31, 41)"
                  title={<span className="titleWithButtons">Edit layout</span>}
                >
                  <div className="contai-edit">
                    <i className="fa-solid fa-pencil" />
                  </div>
                </BoxTooltipTitle>

                <BoxTooltipTitle
                  placement="top"
                  backgroundColor="rgb(27, 31, 41)"
                  title={<span className="titleWithButtons">New layout</span>}
                >
                  <div className="contai-edit">
                    <div className="puls">+</div>
                  </div>
                </BoxTooltipTitle>
              </div>
            </div>
          </div>
        */}
        </div>
      </div>
    </div>
  );
}

export default Layout;
